<template>
	<Tabs value="system" class="tab">
		<Tab-pane label="配置角色" name="system" class="tab-item">
			<Table size="large" :columns="header" :data="list[page-1]"></Table>
			<div class="page">
				<Page placement='top' :total="totalPage" @on-change="pageChange" @on-page-size-change="sizeChange" show-elevator
					  show-sizer
					  show-total :page-size="pageSize" :page-size-opts="[10,20,30,40]"></Page>
			</div>
		</Tab-pane>
		<i-button type="primary" slot="extra" icon="person-add" @click="addRole">新建角色</i-button>
		<i-button type="text" slot="extra" class="extra-btn" icon="ios-undo-outline" @click="$router.go(-1)">返回</i-button>
	</Tabs>
</template>
<script>
	function trim(str, is_global) {
		var result;
		result = str.replace(/(^\s+)|(\s+$)/g, "");
		if (is_global.toLowerCase() == "g") {
			result = result.replace(/\s/g, "");
		}
		return result;
	}
	export default {
		data () {
			return {
				header: [
					{
						type: 'index',
						title: '序号',
						align: 'center',
						width: '80px'
					},
					{
						title: '角色',
						key: 'roleName',
						align: 'center'
					},
					{
						title: '描述',
						key: 'roleComment',
						align: 'center'
					},
					{
						title: '操作',
						key: 'action',
						align: 'center',
						render: (h, params) => {
							let _this = this;

								return h('div', [
									h('i-button', {
										props: {
											type: 'info',
											size: 'small',
											disabled:params.row.roleId==1000?true:false
										},
										style: {
											marginRight: '5px'
										},
										on: {
											click: () => {
												this.$Modal.confirm({
													title: '编辑角色',
													loading: true,
													render: (h) => {
														return h('div', [
															h('p', {
																style: {
																	fontSize: '14px',
																	padding: '10px 0',
																	marginTop: '20px',
																	color: '#323d4d'
																}
															}, '角色名称'),
															h('Input', {
																props: {
																	value: params.row.roleName,
																	placeholder: '输入角色名称'
																},
																on: {
																	input: (val) => {
																		//待编辑输入逻辑
																		_this.roleName = val;
																	}
																}
															}),
															h('p', {
																style: {
																	fontSize: '14px',
																	padding: '10px 0',
																	marginTop: '20px',
																	color: '#323d4d'
																}
															}, '描述'),
															h('Input', {
																props: {
																	value: params.row.roleComment,
																	placeholder: '输入角色描述',
																	type: 'textarea'
																},
																on: {
																	input: (val) => {
																		_this.roleComment = val;
																	}
																}
															}),
														])
													},
													onOk: function () {
														//检测是否为空
														if (_this.roleName.length <= 0 || _this.roleComment.length <= 0) {
															_this.$Modal.remove();
															_this.$Message.error({
																content: '角色名称或描述未编辑！',
																duration: 3
															});
															return false;
														} else {
															let postParams = _.omit(params.row, '_index');
															postParams.roleName = _this.roleName;
															postParams.roleComment = _this.roleComment;
															_this.$http.post('/auth/modifyRole', postParams)
																.then(function (response) {
																	//处理响应数据
																	_this.$Message.success('角色编辑成功！');
																	_this.loadList();
																	_this.$Modal.remove();
																	_this.roleName = '';
																	_this.roleComment = ''
																})
																.catch(function (error) {
																	//处理错误
																	_this.$Message.error({
																		content: error,
																		duration: 3
																	});
																});
														}
													},
													onCancel: function () {
														//点击了取消
														_this.roleName = '';
														_this.roleComment = ''
													}
												})
											}
										}
									}, '编辑角色'),
									h('i-button', {
										props: {
											type: 'ghost',
											size: 'small',
											disabled:params.row.roleId==1000?true:false
										},
										style: {
											marginRight: '5px'
										},
										on: {
											click: () => {
												//跳转到权限页面
												this.$router.push({
													name: 'edituser',
													params: {roleId: params.row.roleId, roleName: params.row.roleName}
												})
											}
										}
									}, '设置默认权限')
								])


						}
					}
				],
				list: [],
				data: [],
				totalPage: 10,
				pageSize: 10,
				page: 1,
				roleName: '',
				roleComment: ''
			}
		},
		created: function () {
			this.loadList();
		},
		methods: {
			addRole: function () {
				let _this = this;
				_this.$Modal.confirm({
					title: '新建角色',
					loading: true,
					render: (h) => {
						return h('div', [
							h('p', {
								style: {
									fontSize: '14px',
									padding: '10px 0',
									marginTop: '20px',
									color: '#323d4d'
								}
							}, '角色名称'),
							h('Input', {
								props: {
									value: _this.roleName,
									placeholder: '输入角色名称'
								},
								on: {
									input: (val) => {
										_this.roleName = trim(val, 'g');
									}
								}
							}),
							h('p', {
								style: {
									fontSize: '14px',
									padding: '10px 0',
									marginTop: '20px',
									color: '#323d4d'
								}
							}, '描述'),
							h('Input', {
								props: {
									value: _this.roleComment,
									placeholder: '输入角色描述',
									type: 'textarea'
								},
								on: {
									input: (val) => {
										_this.roleComment = trim(val, 'g');
									}
								}
							}),
						])
					},
					onOk: function () {
						//检测是否为空
						if (_this.roleName.length <= 0 || _this.roleComment.length <= 0) {
							_this.$Modal.remove();
							_this.$Message.error({
								content: '角色名称或描述不能为空！',
								duration: 3
							});
							_this.roleName = '';
							_this.roleComment = '';
							return false;
						} else {
							_this.$http.post('/auth/creatRole', {
								roleName: _this.roleName,
								roleComment: _this.roleComment
							})
								.then(function (response) {
									//处理响应数据
									if (response.data.code == '1') {//创建成功
										_this.$Message.success('新建角色成功！')
										_this.loadList();
										_this.$Modal.remove();
										_this.roleName = '';
										_this.roleComment = ''
									} else if (response.data.code == '0') {//创建失败
										_this.$Message.error({
											content: response.data.msg,
											duration: 4.5
										});
										_this.$Modal.remove();
										_this.roleName = '';
										_this.roleComment = ''
									}
								})
								.catch(function (error) {
									//处理错误
									_this.$Message.error({
										content: error,
										duration: 3
									});
								});
						}

					},
					onCancel: function () {
						//点击了取消
						_this.roleName = '';
						_this.roleComment = ''
					}
				})
			},
			loadList: function () {
				let _this = this;
				_this.$http.post('/auth/allRoleName', {})
					.then(function (response) {
						//处理响应数据
						let res = response.data;//服务器响应数据
						if(res.data){
							_this.totalPage = _.size(res.data);//设置页码
							_this.data = res.data;//原始数据
							_this.list = _.chunk(res.data, _this.pageSize);//表格数据
						}
					})
					.catch(function (error) {
						//处理错误
						_this.$Message.error({
							content: error,
							duration: 3
						});
					});
			},
			pageChange: function (page) {
				let _this = this;
				if (_this.page != page) {
					_this.page = page;
				}
			},
			sizeChange: function (size) {
				let _this = this;
				_this.pageSize = size;
				_this.list = _.chunk(_this.data, size);
			},
		}
	}
</script>
<style>
	.tab {
		padding-top: 15px;
	}

	.ivu-tabs-tab {
		font-size: 16px;
	}

	.page {
		padding-top: 50px;
		display: flex;
		justify-content: flex-end;
		flex-direction: row;
		padding-bottom: 50px;
	}

	.layout-content .extra-btn {
		font-size: 14px;
		color: #20abff;
	}
</style>



